@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{overflow:hidden;height:100%;-ms-scroll-chaining:none;overscroll-behavior:none;background-color:transparent}a{text-decoration:none !important}button{border:none;border-radius:0px;font-family:'Roboto Mono', monospace;cursor:pointer}body{overflow:auto;height:100%;-ms-scroll-chaining:none;overscroll-behavior:none;background-color:transparent}.App{text-align:center;background-color:black;color:lightgray;height:100vh;padding-bottom:250px;font-family:'Roboto Mono', monospace}.logo{width:60%;padding:10px 10px 100px 10px}.logo-sm{width:200px;padding:10px}.Nav{display:flex;align-items:center;justify-content:space-between;padding:0 100px;position:relative;z-index:1}.Nav::after{position:absolute;content:'';top:0px;right:0px;left:0px;bottom:0px;z-index:-1;background:radial-gradient(circle, rgba(0,0,0,0.2) 0%, rgba(0,0,0,0.5) 100%)}.navLinks{display:flex;flex-direction:row;align-items:flex-end}.navLinks>li{padding:0 20px;list-style:none}.navLinks>li>a{color:lightgray}.navLinks>li>a:hover{color:gray}.navLinks>li:hover{color:gray}.banner{background-image:url(/static/media/banner-69.4bfc3441.jpg);background-position:center;background-repeat:no-repeat;background-size:cover;height:calc(100vh)}.Highlights{flex-direction:column;display:flex;flex-direction:column;align-items:center;justify-content:center}.Highlight{width:100%;height:500px;display:flex;flex-direction:column;align-items:center;justify-content:center;position:relative}.ep-content{width:400px;height:400px;background-image:url(/static/media/candorAlbum.fc7e7191.png);background-position:center;background-repeat:no-repeat;background-size:cover;z-index:2;box-shadow:2px 2px 3px 1px rgba(1,1,1,0.5)}.ep-background{position:absolute;top:0px;right:0px;left:0px;bottom:0px;z-index:1;background-image:url(/static/media/candorAlbum.fc7e7191.png);background-position:center;background-repeat:no-repeat;background-size:cover;filter:blur(10px)}.ep-background::after{content:'';background:radial-gradient(circle, rgba(0,0,0,0.5) 0%, #000 100%);position:absolute;top:0;left:0;width:100%;height:100%;z-index:2}footer{padding-top:20px;display:flex;flex-direction:column;align-items:center}footer button{background-color:palegoldenrod;color:black;font-size:24px;padding:16px 24px;margin:20px 0 32px 0}footer button:hover{background-color:lightgray}footer .logo-footer{width:75px}footer .name{padding:10px}footer .builtBy{font-size:8px}

